import { ColorChip } from "./component/color-chip-components";

(function (blocks, element, data, blockEditor) {
	const el = element.createElement,
		useBlockProps = blockEditor.useBlockProps,
		registerBlockType = blocks.registerBlockType,
		component = wp.components;

	registerBlockType("studio-baang/color-chip", {
		apiVersion: 2,
		title: "Ïª¨Îü¨ Ïπ©",
		category: "design",
		icon: "lightbulb",
		description:
			"Î∏åÎûúÎìú Ïª¨Îü¨ Ïπ©ÏùÑ ÏÉùÏÑ±Ìï©ÎãàÎã§. hexÍ∞íÏùÑ ÏÑ†ÌÉùÌïòÎ©¥ ÏûêÎèôÏúºÎ°ú rgb, cmykÍ∞íÏùÑ Î∞òÌôòÌï©ÎãàÎã§.",

		attributes: {
			name: {
				type: "string",
			},
			color: {
				type: "string",
				default: "#ffffff",
			},
		},

		edit: function (props) {
			const blockProps = useBlockProps();
			const {
				attributes: { name, color },
				setAttributes,
			} = props;

			const editName = (event) => {
				setAttributes({ name: event.target.value });
			};

			const editColor = (val) => {
				setAttributes({ color: val });
			};

			return el(
				"div",
				blockProps,
				el("input", {
					type: "text",
					value: name,
					onChange: editName,
					placeholder: "Ïπ© Ïù¥Î¶ÑÏùÑ Í∏∞ÏûÖÌïòÏÑ∏Ïöî",
				}),
				el(component.ColorPicker, {
					copyFormat: "hex",
					color: color,
					onChange: editColor,
				})
			);
		},

		save: function (props) {
			let content;
			const { name, color } = props.attributes;

			return el(
				ColorChip,
				{
					name,
					color,
				},
				null
			);
		},
	});
})(window.wp.blocks, window.wp.element, window.wp.data, window.wp.blockEditor);
